input {
  width: 300px;
  border: 1px solid #b0c4de;
  border-radius: 4px;
  margin-left: 90px;
  padding: 8px 16px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

button {
  min-width: 100px;
  text-transform: uppercase;
  cursor: pointer;
  background: #b0c4de;
  border: 0;
  border-radius: 4px;
  margin-left: 16px;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  transition: background .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:hover {
  background: #add8e6;
}

.timer {
  gap: 20px;
  font-family: inherit;
  display: flex;
}

.field {
  width: 130px;
  text-align: center;
  font-weight: 500;
}

.value {
  text-transform: uppercase;
